/** 05.FORMS
**********************************/
.form-control {
  position: relative;
  width: 100%;
  margin-bottom: 24px;
}
.form-control input,
.form-control textarea {
  width: 100%;
  height: 48px;
  font-size: 14px;
  padding-top: 20px;
  background: none;
  border: none;
  border-radius: 0;
  color: #333;
  border-bottom: 1px solid #757575;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  -webkit-transition: 0.25s ease-out;
  transition: 0.25s ease-out;
}
.form-control textarea {
  padding-top: 27px;
}
.form-control label {
  position: absolute;
  font-size: 14px;
  color: #999;
  left: 0;
  top: 15px;
  -webkit-transition: 0.2s ease-out;
  -moz-transition: 0.2s ease-out;
  -o-transition: 0.2s ease-out;
  -ms-transition: 0.2s ease-out;
  transition: 0.2s ease-out;
}
.form-control input:focus,
.form-control textarea:focus {
  outline: none;
  border-bottom: 1px solid #008fed;
  -webkit-box-shadow: 0 1px 0 0 #008fed;
  box-shadow: 0 1px 0 0 #008fed;
}
.form-control.active label {
  color: #008fed;
  font-size: 11px;
  -webkit-transform: translateY(-130%);
  -moz-transform: translateY(-130%);
  -ms-transform: translateY(-130%);
  -o-transform: translateY(-130%);
  transform: translateY(-130%);
}
.form-group {
  margin-bottom: 24px;
}
.form-group + .checkbox,
.form-group + .checkbox-inline,
.form-group + .radio,
.form-group + .radio-inline {
  margin-top: 36px;
}
.helper-text {
  font-size: 13px;
}
.helper-link {
  position: absolute;
  top: 50%;
  right: 0;
  font-size: 12px;
  margin-top: -7px;
  text-transform: none;
  opacity: 1;
  visibility: visible;
  -webkit-transition: 0.25s;
  transition: 0.25s;
}
.form-control.active .helper-link {
  opacity: 0;
  visibility: hidden;
}

/* Validation */
label.error {
  display: none !important;
}
.form-control input.error,
.form-control textarea.error {
  color: #e91e63;
  border-color: #e91e63;
  -webkit-box-shadow: 0 1px 0 0 #e91e63;
  box-shadow: 0 1px 0 0 #e91e63;
}
.form-control.active input.error ~ label,
.form-control.active textarea.error ~ label {
  color: #e91e63;
}
.form-control input.valid,
.form-control textarea.valid {
  border-color: #96cb4b;
  -webkit-box-shadow: 0 1px 0 0 #96cb4b;
  box-shadow: 0 1px 0 0 #96cb4b;
}
.form-control.active input.valid ~ label,
.form-control.active textarea.valid ~ label {
  color: #96cb4b;
}
.form-control input.error ~ .helper-link,
.form-control input.valid ~ .helper-link {
  opacity: 0;
  visibility: hidden;
}
.error-label,
.valid-label {
  position: absolute;
  top: 50%;
  font-weight: 300;
  right: 0;
  font-size: 26px;
  margin-top: -13px;
  opacity: 0;
  visibility: hidden;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
}
.error-label:before,
.valid-label:before {
  font-family: "Flaticon";
}
.error-label:before {
  color: #e91e63;
  content: "!";
  padding-right: 10px;
}
.valid-label:before {
  color: #96cb4b;
  content: "\e048";
}
.form-control input.error ~ .error-label,
.form-control textarea.error ~ .error-label,
.form-control input.valid ~ .valid-label,
.form-control textarea.valid ~ .valid-label {
  opacity: 1;
  visibility: visible;
}
.response-holder {
  visibility: hidden;
  opacity: 0;
  padding: 12px 0;
  font-size: 13px;
  line-height: 1.8;
  -webkit-transition: all 0.4s;
  transition: all 0.4s;
}
.response-holder.error,
.response-holder.success {
  visibility: visible;
  opacity: 1;
}
.response-holder.error {
  color: #e91e63;
}
.response-holder.success {
  color: #96cb4b;
}

/* Custom Checkboxes and Radios */
.checkbox,
.radio,
.checkbox-inline,
.radio-inline {
  position: relative;
  display: block;
  font-size: 14px;
  color: #999;
  cursor: default;
  padding-left: 27px;
  margin-bottom: 10px;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
label.hover {
  cursor: pointer;
}
.checkbox-inline,
.radio-inline {
  display: inline-block;
  margin-right: 18px;
}
.text-right .checkbox-inline,
.text-right .radio-inline {
  margin-left: 15px;
  margin-right: 0;
}
.icheckbox,
.iradio {
  position: absolute;
  z-index: 0;
  width: 18px;
  height: 18px;
  vertical-align: middle;
  left: 0;
  top: -2px;
  border: 2px solid #757575;
  border-radius: 1px;
  background-clip: padding-box;
  cursor: pointer;
  -webkit-transition: 0.2s;
  transition: 0.2s;
}
.icheckbox.checked,
.radio-alt .iradio.checked {
  left: -6px;
  top: -8px;
  width: 12px;
  height: 22px;
  border-top: 2px solid transparent;
  border-left: 2px solid transparent;
  border-right: 2px solid #96cb4b;
  border-bottom: 2px solid #96cb4b;
  -webkit-transform: rotateZ(40deg);
  -moz-transform: rotateZ(40deg);
  -ms-transform: rotateZ(40deg);
  -o-transform: rotateZ(40deg);
  transform: rotateZ(40deg);
  -webkit-backface-visibility: hidden;
  -webkit-transform-origin: 100% 100%;
  -moz-transform-origin: 100% 100%;
  -ms-transform-origin: 100% 100%;
  -o-transform-origin: 100% 100%;
  transform-origin: 100% 100%;
}
.radio-alt .iradio {
  border-radius: 1px;
}
.iradio,
.iradio:before {
  border-radius: 50%;
  -webkit-transition: 0.25s ease;
  transition: 0.25s ease;
}
.iradio:before {
  position: absolute;
  display: block;
  content: "";
  width: 8px;
  height: 8px;
  background-color: #96cb4b;
  opacity: 0;
  top: 3px;
  left: 3px;
  -webkit-transform: scale(0);
  transform: scale(0);
}
.iradio.checked {
  border-color: #96cb4b;
}
.iradio.checked:before {
  opacity: 1;
  -webkit-transform: scale(1);
  transform: scale(1);
}
.icheckbox.disabled,
.iradio.disabled {
  border-color: #cccccc;
  background-color: #cccccc;
  cursor: default;
}
.text-right .checkbox-inline,
.text-right .radio-inline {
  margin-left: 25px;
  margin-right: 0;
}
.light-color .checkbox,
.light-color .radio,
.light-color .checkbox-inline,
.light-color .radio-inline {
  color: #fff;
}
.light-color .icheckbox,
.light-color .iradio {
  border-color: #fff;
}
.light-color .icheckbox:before,
.light-color .radio-alt .iradio:before {
  color: #3a3a3a;
}
.light-color .iradio:before {
  background-color: #3a3a3a;
}
.light-color .size-lg .iradio:before,
.light-color.size-lg .iradio:before {
  background-color: #5e5e5e;
}
.light-color .size-lg .icheckbox:before,
.light-color.size-lg .icheckbox:before,
.light-color .size-lg.radio-alt .iradio:before,
.light-color.size-lg.radio-alt .iradio:before {
  color: #5e5e5e;
}
.light-color .radio-alt .iradio:before {
  background-color: transparent;
}
.icheckbox.disabled,
.iradio.disabled {
  border-color: #fff;
  background-color: #fff;
  opacity: .6;
}

/* Search Field */
.search-field {
  position: relative;
  display: block;
  width: 100%;
  max-width: 260px;
  padding-left: 40px;
}
.search-field label {
  left: 40px;
}
.search-field button[type=submit] {
