/** 07.INTRO
**********************************/

.intro {
  position: fixed;
  width: 100%;
  min-height: 100%;
  top: 0;
  left: 0;
  z-index: 1;
  color: #fff;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  -webkit-backface-visibility: hidden;
}
.touch .intro {
  position: relative;
}
.fixed-footer .intro.transparent {
  opacity: 0;
}
.intro .container {
  position: relative;
  z-index: 10;
}

/* Social Buttons */
.intro .social-buttons,
.offcanvas-nav .social-buttons {
  position: absolute;
  top: 0;
  right: 0;
  padding-top: 36px;
}
.intro .social-buttons a,
.offcanvas-nav .social-buttons a,
.navbar .social-buttons a {
  color: #fff;
}

/* Navi */
.intro .navi {
  position: absolute;
  top: 23px;
  left: 0;
  font-size: 14px;
}
.intro .navi a {
  color: #fff;
}
.intro .navi a:focus {
  opacity: 1;
}
.nav-toggle {
  display: inline-block;
  vertical-align: middle;
  position: relative;
  width: 53px;
  height: 53px;
  color: #fff;
  cursor: pointer;
  border-radius: 50%;
  margin-right: 20px;
}
.intro .nav-toggle span {
  display: block;
  position: absolute;
  z-index: 2;
  background-color: #008fed;
  border-radius: 50%;
  line-height: 56px;
  width: 56px;
  height: 56px;
  top: -1px;
  left: -1px;
  -webkit-transform: translateZ(0);
  -moz-transform: translateZ(0);
  -ms-transform: translateZ(0);
  -o-transform: translateZ(0);
  transform: translateZ(0);
  -webkit-mask-image: -webkit-radial-gradient(circle, #ffffff 100%, #000000 100%);
}
.android .intro .nav-toggle span {
  -webkit-mask-image: none;
  -webkit-transition: background-color .2s;
  transition: background-color .2s;
}
.android .intro .nav-toggle:active span {
  background-color: #1fa4fc;
}
.android .intro .nav-toggle span.waves-effect {
  -webkit-transition: none;
  transition: none;
}
.android .intro .nav-toggle span.waves-effect .waves-ripple {
  display: none !important;
}
.nav-toggle i {
  font-size: 23px;
  margin: 0;
}
.intro .nav-toggle:before {
  position: absolute;
  z-index: 1;
  display: block;
  content: "";
  width: 53px;
  height: 53px;
  top: 1px;
  left: 1px;
  border-radius: 50%;
  -webkit-box-shadow: 0px 3px 6px 2px rgba(0, 0, 0, 0.25);
  box-shadow: 0px 3px 6px 2px rgba(0, 0, 0, 0.25);
  -webkit-transition: all .3s;
  transition: all .3s;
}
.intro .nav-toggle-float:active:before {
  -webkit-box-shadow: 0px 8px 20px 1px rgba(0, 0, 0, 0.3);
  box-shadow: 0px 8px 20px 1px rgba(0, 0, 0, 0.3);
}

/* Into Columns */
.column-wrap {
  display: table;
  width: 100%;
}
.intro .column {
  position: relative;
  display: table-cell;
  vertical-align: bottom;
  z-index: 10;
  text-align: center;
}
.intro .column.c-left,
.intro .column.c-right {
  width: 30%;
}
.intro .column.c-middle {
  width: 40%;
}

/* Logo */
.intro .logo {
  text-align: center;
  padding-top: 20px;
  margin-bottom: 40px;
  -webkit-transform: translate(0, 0);
  transform: translate(0, 0);
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
}
.intro .logo img {
  display: block;
  width: 100%;
  max-width: 178px;
  margin: 0 auto 30px auto;
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
}
.intro .logo span {
  display: block;
  font-size: 16px;
  margin-top: 25px;
  opacity: .5;
}

/* Phone */
.intro .phone {
  position: relative;
  width: 370px;
  height: 683px;
  background-image: url('/Content/Images/intro/nexus.png');
  background-repeat: no-repeat;
  background-size: cover;
  padding: 52px 20px 0 21px;
  margin: 0 auto -380px auto;
  overflow: hidden;
  -webkit-transform: translateY(0) scale(1);
  transform: translateY(0) scale(1);
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
  -webkit-backface-visibility: hidden;
}
.intro .phone img {
  display: block;
  width: 100%;
  max-width: 100%;
}
.intro .intro-features {
  -webkit-transform: translateY(-120px);
  transform: translateY(-120px);
  -webkit-backface-visibility: hidden;

}

/* Scroll for More Btn */
.scroll-more {
  display: inline-block;
  height: 36px;
  color: #fff;
  text-transform: none;
  text-decoration: none;
  font-size: 14px;
  margin-bottom: 65px;
  -webkit-backface-visibility: hidden;
}
.scroll-more:hover,
.scroll-more:focus {
  color: #fff;
  text-decoration: none;
}
.scroll-more span {
  display: inline-block;
  vertical-align: middle;
  font-weight: 300;
}
.scroll-more .icon {
  position: relative;
  display: inline-block;
  width: 36px;
  height: 36px;
  vertical-align: middle;
  border: 1px solid #fff;
  border-radius: 50%;
  margin-right: 8px;
  padding: 8px 0 0 12px;
}
.scroll-more .icon:before {
  display: block;
  width: 10px;
  height: 18px;
  content: "";
  border: 1px solid #fff;
  border-radius: 12px;
}
.scroll-more .icon:after {
  position: absolute;
  display: block;
  top: 11px;
  left: 16px;
  width: 2px;
  height: 4px;
  content: "";
  background-color: #fff;
  -webkit-animation: scroll 1.2s infinite;
  animation: scroll 1.2s infinite;
}
@-webkit-keyframes scroll {
  0% { opacity: .8; -webkit-transform: translateY(0); }
  60% { opacity: .6; }
  100% { opacity: 0; -webkit-transform: translateY(8px); }
}
@-moz-keyframes scroll {
  0% { opacity: .8; -moz-transform: translateY(0); }
  60% { opacity: .6; }
  100% { opacity: 0; -moz-transform: translateY(8px); }
}
@-ms-keyframes scroll {
  0% { opacity: .8; -ms-transform: translateY(0); }
  60% { opacity: .6; }
  100% { opacity: 0; -ms-transform: translateY(8px); }
}
@keyframes scroll {
  0% { opacity: .8; transform: translateY(0); }
  60% { opacity: .6; }
  100% { opacity: 0; transform: translateY(8px); }
}
.intro .intro-features .icon-block {
  opacity: 0;
  -webkit-transform: translateX(50px);
  transform: translateX(50px);
  -webkit-transition: opacity .5s, -webkit-transform .5s;
  transition: opacity .5s, transform .5s;
}

