  transition: all 0.3s;
}
.offcanvas-nav .search-box .search-btn {
  border: none;
  background: none;
  opacity: 0;
  visibility: hidden;
  -webkit-transition: all 0.2s;
  transition: all 0.2s;
}
.offcanvas-nav .search-box .search-toggle {
  border-radius: 3px;
  opacity: 1;
  visibility: visible;
  padding-top: 7px;
  left: auto;
