}
.page-slider:before,
.page-slider:after,
.page-slider .slider:before,
.page-slider span:before,
.page-slider span:after {
  position: absolute;
  z-index: 1;
  display: block;
  content: "";
  width: 2px;
  height: 2px;
  background-color: #000;
  top: 7px;
}
.page-slider:before {
  left: 0;
}
.page-slider:after {
  right: 0;
}
.page-slider .slider:before {
  left: 50%;
}
.page-slider span:before {
  left: 25%;
}
.page-slider span:after {
  right: 25%;
}
.page-slider .tooltip {
  width: 36px;
  height: 36px;
  background-color: #008fed;
  border-radius: 50%;
  line-height: 1;
  margin-top: -47px !important;
  opacity: 0;
  -webkit-transition: opacity .25s, visibility .25s;
  transition: opacity .25s, visibility .25s;
}
